<template lang="html">
  <div class="radio" @mouseover="isControlShow = true" @mouseout="isControlShow = false">
    <input type="text"  placeholder="单选题-请输入内容" class="input-text input-title"
    v-model="radioData.itemVal" :class="{'valIsNull': isValNotNullShow, 'valNotNull': radioData.itemVal}">
    <ul>
      <li v-for="radioVal of radioData.radioValArry" track-by="$index">
        <div class="option">
          <span class="glyphicon glyphicon-trash" aria-hidden="true" title="删除该项" @click="delOption($index)"></span>
          <input type="radio" value="{{ radioVal }}" >
          <input type="text"  placeholder="选项{{$index + 1}}-请输入内容" class="input-text"
          v-model="radioVal" :class="{'valIsNull': isValNotNullShow, 'valNotNull': radioVal}">
        </div>
      </li>
    </ul>
    <button type="button" class="btn btn-default btn-sm" @click="addMoreOption">添加更多选项</button>
    <div class="control" :class="{ 'controlShow' : isControlShow }">
      <button type="button"  @click="upMove" v-if=" index !== 0 ">上移</button>
      <button type="button"  @click="downMove" v-if=" index !== (list.length - 1) ">下移</button>
      <!-- <button type="button" @click="copyItem">复用</button> -->
      <button type="button"  @click="delItem">删除</button>
      <label><input type="checkbox" v-model="radioData.notNull"> 此题是否必填</label>
    </div>
  </div>

</template>

<script>
export default {
  data () {
    return {
      radioData: {
        id: null,            // 题目标示ID
        radioValArry: new Array(2), // 选项的值
        itemVal: '',                // 标题的值
        notNull: true,              // 必填标示
        pickedVal: ''
      },
      isControlShow: false,  // 控制是否显示上下移动按钮
      isValNotNullShow: false     // 为空的项 展示效果判断
    }
  },
  ready: function () {
    this.radioData.id = this.issueDate._uid
    if (this.issueDate.date) {
      this.radioData.radioValArry = this.issueDate.date.radioValArry
      this.radioData.itemVal = this.issueDate.date.itemVal
      this.radioData.notNull = this.issueDate.date.notNull
    }
  },
  events: {
    'pub-msg': function (msg) {
      if (msg === 'pub') {
        this.isValNotNullShow = true
        this.notify()
      }
    },
    // 保存数据消息
    'save': function () {
      this.$dispatch('item-data', this.radioData)
    }
  },
  methods: {
    // 新增问题选择项
    addMoreOption: function () {
      this.radioData.radioValArry.push('选项' + (this.radioData.radioValArry.length + 1) + '-请输入内容')
    },
    // 删除问题选择项
    delOption: function (index) {
      this.radioData.radioValArry.splice(index, 1)
    },
    // 上移动该问题
    upMove: function () {
      let temp = this.list[this.index]
      this.list.$set(this.index, this.list[this.index - 1])
      this.list.$set(this.index - 1, temp)
    },
    // 下移动该问题
    downMove: function () {
      let now = this.list[this.index]
      let next = this.list[this.index + 1]
      this.list.splice(this.index, 1, next)
      this.list.splice(this.index + 1, 1, now)
    },
    // 删除该问题
    delItem: function () {
      this.list.splice(this.index, 1)
    },
    // // 复用该问题
    // copyItem: function () {
    //   let uid = new Date().getTime()
    //   this.list.splice(this.index + 1, 0, {_uid: uid, type: 'radio'})
    // },
    // 通知父组件 值是否都不为空
    notify: function () {
      let isValNotNull = false
      let radioValArryNotnull = true
      for (var i = 0; i < this.radioData.radioValArry.length; i++) {
        if (!this.radioData.radioValArry[i]) radioValArryNotnull = false
      }
      if (this.radioData.itemVal && radioValArryNotnull) {
        isValNotNull = 'ok'
        this.$dispatch('item-data', this.radioData)
      } else {
        isValNotNull = 'no'
      }
      this.$dispatch('child-msg', isValNotNull)
    }
  },
  // 父组件的数据
  props: ['index', 'list', 'issueDate']
}

</script>

<style lang="css" scoped>
.input-text{
  width: 90%;
  outline: none;
}
input{
  border: none;
}
.option{
  margin: 5px 0 5px 20px;
}
.input-title{
  font-size: 18px;
  font-weight: bold;
}
.control{
  width:250px;
  font-size: 12px;
  float: right;
  opacity: 0;
}
.control button{
  border: none;
  background-color: #fff;
  outline: none;
}
.control button:hover{
  background-color: #feefe5;
}
.controlShow{
  opacity: 1;
}
.radio ul{
  margin-left: -40px;
}
.radio li{
  list-style-type:none
}
.glyphicon{
  margin-left: -20px;
  margin-right: 25px;
  cursor:pointer;
}
.glyphicon:hover{
  color: #f00;
}
.valIsNull{
  outline: 1px solid #f00;
}
.valNotNull{
  outline: none;
}
</style>



