<template lang="html">
  <div class="text" @mouseover="isControlShow = true" @mouseout="isControlShow = false">
    <input type="text"  placeholder="文本题-请输入问题？" class="input-text input-title"
    v-model="textDate.itemVal" :class="{'valIsNull': isValNotNullShow, 'valNotNull': textDate.itemVal}">
    <div class="control" :class="{ 'controlShow' : isControlShow }">
      <button type="button" @click="upMove" v-if=" index !== 0 ">上移</button>
      <button type="button" @click="downMove" v-if=" index !== (list.length - 1) ">下移</button>
      <!-- <button type="button" @click="copyItem">复用</button> -->
      <button type="button" @click="delItem">删除</button>
      <label><input type="checkbox"  v-model="textDate.notNull"> 此题是否必填</label>
    </div>
  </div>

</template>

<script>
export default {
  data () {
    return {
      textDate: {
        id: null,   // 题目标示ID
        itemVal: '',       // 标题的值
        notNull: true,    // 必填标示
        pickedVal: ''
      },
      isControlShow: false,  // 控制是否显示上下移动按钮
      isValNotNullShow: false     // 为空的项 展示效果判断
    }
  },
  ready: function () {
    this.textDate.id = this.issueDate._uid
    if (this.issueDate.date) {
      this.textDate.itemVal = this.issueDate.date.itemVal
      this.textDate.notNull = this.issueDate.date.notNull
    }
  },
  events: {
    'pub-msg': function (msg) {
      if (msg === 'pub') {
        this.isValNotNullShow = true
        this.notify()
      }
    },
    // 保存数据消息
    'save': function () {
      this.$dispatch('item-data', this.textDate)
    }
  },
  methods: {
    // 上移动该问题
    upMove: function () {
      let temp = this.list[this.index]
      this.list.$set(this.index, this.list[this.index - 1])
      this.list.$set(this.index - 1, temp)
    },
    // 下移动该问题
    downMove: function () {
      let now = this.list[this.index]
      let next = this.list[this.index + 1]
      this.list.splice(this.index, 1, next)
      this.list.splice(this.index + 1, 1, now)
    },
    // 删除该问题
    delItem: function () {
      this.list.splice(this.index, 1)
    },
    // // 复用该问题
    // copyItem: function () {
    //   let uid = new Date().getTime()
    //   this.list.splice(this.index + 1, 0, {_uid: uid, type: 'text'})
    // },
    // 通知父组件 值是否都不为空
    notify: function () {
      let isValNotNull = -1
      if (this.textDate.itemVal) {
        isValNotNull = 'ok'
        this.$dispatch('item-data', this.textDate)
      } else {
        isValNotNull = 'no'
      }
      this.$dispatch('child-msg', isValNotNull)
    }
  },
  // 父组件的数据
  props: ['index', 'list', 'issueDate']
}
</script>

<style lang="css" scoped>
.input-text{
  width: 90%;
  outline: none;
}
input{
  border: none;
}
textarea{
  outline: none;
  width: 60%;
  height: 100px;
  margin: 5px 0;
  border: 1px solid #d6d2d2;
}
.input-title{
  font-size: 18px;
  font-weight: bold;
}
.control{
  width: 250px;
  float: right;
  font-size: 12px;
  opacity: 0;
}
.control button{
  border: none;
  background-color: #fff;
  outline: none;
}
.control button:hover{
  background-color: #feefe5;
}
.controlShow{
  opacity: 1;
}
.valIsNull{
  outline: 1px solid #f00;
}
.valNotNull{
  outline: none;
}
</style>



