<template lang="html">
  <head-nav></head-nav>
  <div class="container">
    <div class="head">
      <input type="text" placeholder="添加标题" v-model="issueDate.itemTitle">
    </div>
    <div class="centre">
      <div class="item">
        <ol>
          <li v-for="item of issueDate.itemList" track-by="_uid">
            <component :is="item.type" :index="$index" :list="issueDate.itemList" :issue-date="item"></component>
          </li>
        </ol>
      </div>
      <div class="addIssue">
        <div class="item-type">
          <button type="button" name="button" @click="addRadio">
            <span class="glyphicon glyphicon-record" aria-hidden="true"></span> 单选
          </button>
          <button type="ridao" name="button" @click="addCheckbox">
            <span class="glyphicon glyphicon-ok-circle" aria-hidden="true"></span> 多选
          </button>
          <button type="button" name="button" @click="addText">
            <span class="glyphicon glyphicon-text-size" aria-hidden="true"></span> 文本题
          </button>
        </div>
        <button type="button" name="button" class="add">
          <span class="glyphicon glyphicon-plus" aria-hidden="true"></span> 添加问题
        </button>
      </div>
    </div>
    <div class="foot">
      <div class="datePicker">
        <label>问卷截止日期：</label>
        <br>
        <div id="calendar">
          <div class='date-input'>
            <i class='date-iconfont'>&#xe600;</i>
            <input type='text' name='date-input' class='form-control' :value="issueDate.endDate" readonly>
          </div>
        </div>
      </div>
      <div class="save">
        <a class="btn btn-default" v-link="{name: 'index'}" role="button" @click="saveQuestionnaire()">保存问卷</a>
        <button type="button" @click="pubIssue" class="btn btn-default" data-toggle="modal" data-target="#myModal">发布问卷</button>
      </div>
      <!-- Modal-全选删除 -->
      <div class="modal fade" id="myModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
        <div class="modal-dialog modal-sm" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
              <h4 class="modal-title" id="myModalLabel">提示</h4>
            </div>
            <div class="modal-body error" v-if="itemListNotNull.list">您未添加任何问题，无法发布！</div>
            <div class="modal-body error" v-if="itemListNotNull.title">请给问卷添加一个标题！</div>
            <div class="modal-body " v-if="itemListNotNull.succeed"> 提交成功</div>
            <div class="modal-body error" v-if="itemListNotNull.issue "> 问题或者选项不能为空！请仔细检查！</div>
            <div class="modal-body error" v-if="itemListNotNull.date"> 请检查截止时间是否设置有误？或者为空？</div>
            <div class="modal-footer">
              <button type="button" class="btn btn-default" data-dismiss="modal" v-if="!itemListNotNull.succeed">确认</button>
              <a class="btn btn-default" data-dismiss="modal" v-link="{name: 'index'}" role="button" v-if="itemListNotNull.succeed" @click="pubDate(true)">确认发布</a>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import headNav from './headNav'
import radio from './itemType/radio'
import checkbox from './itemType/checkbox'
import text from './itemType/text'
import $ from 'jquery'
var DatePicker = require('../assets/lib/js/datePicker.js')
export default {
  components: {
    headNav,
    radio,
    checkbox,
    text
  },
  data () {
    return {
      issueDate: {
        userID: null,             // 用户ID
        issueListId: 0,           // 问卷列表ID
        itemTitle: null,          // 问卷标题
        itemList: [],             // 问题类型列表
        endDate: null,             // 截止时间
        addTime: null             // 创建时间
      },
      childValNotNull: -1,      // 检查问题子组件 空值
      itemListNotNull: {        // 控制modal 信息提示
        title: true,            // 标题错误
        list: true,             // 问题列表为空
        issue: false,           // 问题错误
        date: true,             // 时间为空
        succeed: false          // 成功没有错误
      }
    }
  },
  created: function () {
    let userID = this.$route.query.userID
    this.issueDate.userID = userID
    let issueListId = this.$route.query.issueListId
    let date = JSON.parse(global.localStorage.getItem('issueDate'))
    if (!date) return
    if (issueListId) {
      this.issueDate.issueListId = issueListId
      this.issueDate.itemList = date.issueList[issueListId].itemList
      this.issueDate.endDate = date.issueList[issueListId].endDate
      this.issueDate.addTime = date.issueList[issueListId].addTime
      this.issueDate.itemTitle = date.issueList[issueListId].itemTitle
    } else {
      if (date.issueList.length) {
        this.issueDate.issueListId = date.issueList.length
      } else {
        this.issueDate.issueListId = 0
      }
    }
  },
  ready: function () {
    let starDate = new Date().getFullYear() + '-' + (new Date().getMonth() + 1) + '-' +
                   new Date().getDate()
    let date = new DatePicker(
      {
        id: '#calendar',
        minDate: starDate
      }
    )
    date.setAndGetDate().getDate()
  },
  methods: {
    addRadio: function () {
      let uid = new Date().getTime()
      this.issueDate.itemList.push({_uid: uid, type: 'radio'})
    },
    addCheckbox: function () {
      let uid = new Date().getTime()
      this.issueDate.itemList.push({_uid: uid, type: 'checkbox'})
    },
    addText: function () {
      let uid = new Date().getTime()
      this.issueDate.itemList.push({_uid: uid, type: 'text'})
    },
    pubIssue: function () {
      // 每一次都要重置信息提示控制
      this.itemListNotNull.issue = false
      this.itemListNotNull.succeed = false
      this.itemListNotNull.title = true
      this.itemListNotNull.date = true
      this.itemListNotNull.list = true
      this.$broadcast('pub-msg', 'pub')   // 给问题组件 发送消息 检查是否有空值
      this.issueDate.endDate = $('#calendar .form-control').val()
      if (this.issueDate.itemList.length) this.itemListNotNull.list = false      // 没添加问题
      if (this.issueDate.itemTitle) this.itemListNotNull.title = false      // 没添加问卷标题
      if (this.issueDate.endDate) {
        if (new Date(nowTime()) < new Date(this.issueDate.endDate)) this.itemListNotNull.date = false
      }
      if (!this.itemListNotNull.list && !this.itemListNotNull.title &&
          !this.itemListNotNull.date) {
        if (!this.itemListNotNull.issue) this.itemListNotNull.succeed = true
      }
    },
    // 确认发布存储数据
    pubDate: function (isPub) {
      let tempDate = this.issueDate
      if (!this.issueDate.addTime) {
        this.issueDate.addTime = nowTime()
      }
      let issueDate = {
        issueListId: tempDate.issueListId,        // 问题列表ID
        itemList: tempDate.itemList,              // 问题类型列表
        endDate: tempDate.endDate,                // 截止时间
        itemTitle: tempDate.itemTitle,
        addTime: tempDate.addTime
      }
      issueDate.state = isPub ? '已发布' : '未发布'
      issueDate.questionnaireID = tempDate.issueListId + tempDate.userID
      let data = JSON.parse(global.localStorage.getItem('issueDate'))
      if (data) {
        if (data.userID === tempDate.userID) {
          data.issueList[tempDate.issueListId] = issueDate
        }
      } else {
        data = {
          userID: tempDate.userID,
          issueList: [issueDate]
        }
      }
      global.localStorage.setItem('issueDate', JSON.stringify(data))
    },
    saveQuestionnaire: function () {
      this.$broadcast('save')   // 给问题组件 发送消息 传送数据
      this.pubDate(false)            // 保存数据
    }
  },
  events: {
    'child-msg': function (msg) {
      if (msg === 'no') this.itemListNotNull.issue = true
    },
    'item-data': function (msg) {
      for (var i = 0; i < this.issueDate.itemList.length; i++) {
        if (this.issueDate.itemList[i]._uid === msg.id) {
          this.issueDate.itemList[i].date = msg
        }
      }
    }
  }
}
// 获取当前时间字符串
function nowTime () {
  let nowDate = new Date()
  return nowDate.getFullYear() + '-' + (nowDate.getMonth() + 1) + '-' +
         nowDate.getDate() + ' ' + nowDate.getHours() + ':' + nowDate.getMinutes() + ':' +
         nowDate.getSeconds()
}
</script>

<style lang="css" scoped>
.container{
  margin: 20px auto;
  background-color: #fff;
  padding: 10px;
  font-size: 12px;
  box-shadow: 0 0  6px 2px #999;
}
.head{
  width: 100%;
  height: 50px;
}
.head input{
  border: none;
  width: 100%;
  height: 50px;
  font-size: 18px;
  text-align: center;
  line-height: 50px;
  font-weight: bold;
}
.head input:hover{
  background-color: #feefe5;
}
.centre{
  padding: 15px;
  width: 100%;
  border-top: 1px solid #c9c0c0;
  border-bottom: 1px solid #c9c0c0;
}
.centre .item{
  font-size: 16px;
}
.centre .item ol{
  margin-bottom: 40px;
}
.centre .item li{
  margin-bottom: 20px;
}
.centre .addIssue{
  width: 100%;
  height: 100%;
  border: 1px solid #d6d2d2;
}
.centre .addIssue button{
  background-color: #eee;
  border: none;
  outline: none;
}
.centre .addIssue .item-type button:hover{
  background-color: #feefe5;
}
.centre .addIssue .item-type{
  width: 100%;
  height: 46px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.centre .addIssue .item-type button{
  width: 80px;
  height: 25px;
  margin: 0 10px;
  line-height: 25px;
}
.centre .addIssue .add{
  width: 100%;
  height: 70px;
  font-size: 18px;
}
.foot{
  padding: 20px 20px 70px 20px;
}
.foot button, input{
  outline: none;
}
.foot .datePicker, .save{
  display: inline-block;
}
.foot .datePicker{
  float: left;
  margin: 0px 10px 0px 5%;
}
.foot .save{
  float: right;
  margin: 18px 5% 10px 10px;
}
.foot .save button{
    background-color: #fff;
    border: 1px solid #c2c0c0;
    margin: 0 5px;
}
.foot .save button:hover, a:hover{
    color: #f5f5f5;
    background-color: #ee6a01;
}
.error{
  color: #f00;
}
</style>



